import React, { useState, useEffect, useRef } from 'react';
import './Chat.css';
import { database, auth } from '../../firebase';
import { ref, push, onValue, serverTimestamp, set } from 'firebase/database';
import { signInAnonymously } from 'firebase/auth';
import { startTelegramPolling, setActiveChat } from '../../services/telegramPolling';

//   
const sendTelegramNotification = async (message: string) => {
  const TELEGRAM_BOT_TOKEN = process.env.REACT_APP_TELEGRAM_BOT_TOKEN;
  const TELEGRAM_CHAT_ID = process.env.REACT_APP_TELEGRAM_CHAT_ID;
  
  const telegramMessage = `
 DIORA   

 : ${message}
 : 
 : ${new Date().toLocaleString('ko-KR')}

  .
  `.trim();

  const response = await fetch(`https://api.telegram.org/bot${TELEGRAM_BOT_TOKEN}/sendMessage`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      chat_id: TELEGRAM_CHAT_ID,
      text: telegramMessage,
      parse_mode: 'HTML'
    })
  });

  return response.json();
};

interface Message {
  id: string;
  text: string;
  sender: 'customer' | 'admin';
  timestamp: number;
}

const Chat: React.FC = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [messages, setMessages] = useState<Message[]>([]);
  const [inputText, setInputText] = useState('');
  const [chatId, setChatId] = useState<string | null>(null);
  const [isTyping] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  //      
  useEffect(() => {
    const initChat = async () => {
      try {
        //  
        const userCredential = await signInAnonymously(auth);
        const userId = userCredential.user.uid;
        
        //   
        const newChatRef = push(ref(database, 'chats'));
        setChatId(newChatRef.key);
        
        //   
        startTelegramPolling();
        
        //     
        if (newChatRef.key) {
          setActiveChat(newChatRef.key);
        }

        //    
        await set(ref(database, `chats/${newChatRef.key}/info`), {
          userId,
          startTime: serverTimestamp(),
          status: 'active'
        });

        //  
        const welcomeMessage: Message = {
          id: 'welcome',
          text: '! DIORA.  ?',
          sender: 'admin',
          timestamp: Date.now()
        };
        setMessages([welcomeMessage]);

      } catch (error) {
        console.error('Chat initialization error:', error);
      }
    };

    if (isOpen && !chatId) {
      initChat();
    }
  }, [isOpen, chatId]);

  //  
  useEffect(() => {
    if (!chatId) return;

    const messagesRef = ref(database, `chats/${chatId}/messages`);
    const unsubscribe = onValue(messagesRef, (snapshot) => {
      const data = snapshot.val();
      if (data) {
        const messagesList = Object.entries(data).map(([key, value]: [string, any]) => ({
          id: key,
          ...value
        }));
        setMessages(prev => [
          prev[0], //   
          ...messagesList.sort((a, b) => a.timestamp - b.timestamp)
        ]);
      }
    });

    return () => unsubscribe();
  }, [chatId]);

  //  
  const sendMessage = async () => {
    if (!inputText.trim() || !chatId) return;

    const messageData = {
      text: inputText,
      sender: 'customer',
      timestamp: serverTimestamp()
    };

    try {
      await push(ref(database, `chats/${chatId}/messages`), messageData);
      setInputText('');
      
      //   
      try {
        await sendTelegramNotification(inputText);
      } catch (error) {
        console.error('  :', error);
      }
      
    } catch (error) {
      console.error('  :', error);
    }
  };

  //  
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  return (
    <>
      {/*   */}
      <button 
        className="chat-floating-button"
        onClick={() => setIsOpen(true)}
        style={{ display: isOpen ? 'none' : 'flex' }}
      >
        <span className="chat-icon"></span>
        <span className="chat-badge"> </span>
      </button>

      {/*  */}
      {isOpen && (
        <div className="chat-container">
          <div className="chat-header">
            <div className="chat-header-info">
              <h3>DIORA  </h3>
              <span className="chat-status"> </span>
            </div>
            <button 
              className="chat-close"
              onClick={() => setIsOpen(false)}
            >
              
            </button>
          </div>

          <div className="chat-messages">
            {messages.map((message) => (
              <div 
                key={message.id}
                className={`chat-message ${message.sender}`}
              >
                <div className="chat-bubble">
                  {message.text}
                </div>
                <div className="chat-time">
                  {message.timestamp ? new Date(message.timestamp).toLocaleTimeString('ko-KR', {
                    hour: '2-digit',
                    minute: '2-digit'
                  }) : ''}
                </div>
              </div>
            ))}
            {isTyping && (
              <div className="chat-message admin">
                <div className="chat-bubble typing">
                  <span></span>
                  <span></span>
                  <span></span>
                </div>
              </div>
            )}
            <div ref={messagesEndRef} />
          </div>

          <div className="chat-input-container">
            <input
              type="text"
              className="chat-input"
              placeholder=" ..."
              value={inputText}
              onChange={(e) => setInputText(e.target.value)}
              onKeyPress={(e) => e.key === 'Enter' && sendMessage()}
            />
            <button 
              className="chat-send"
              onClick={sendMessage}
              disabled={!inputText.trim()}
            >
              
            </button>
          </div>
        </div>
      )}
    </>
  );
};

export default Chat;